<template>
	<view class="registerbg">
		<view class="head">
			<view class="Size">注册账号</view>
		</view>
		<view class="body">
			<view class="input_box">
				<view class="left">
					<u-icon size="32" name="/static/img/22.png" class="footIcon" v-if="zhshow"></u-icon>
					<u-icon size="32" name="/static/img/22s.png" class="footIcon" v-else></u-icon>
				</view>
				<view class="center">
					<u-input type="text" placeholder="账号" v-model="form.username" @input="onInput"/>
				</view>
				<view class="right">
					<u-icon size="20" name="/static/img/4.png" class="footIcon" v-if="zhshow"></u-icon>
					<u-icon size="20" name="/static/img/4s.png" class="footIcon" v-else></u-icon>
				</view>
			</view>
			<view class="input_box">
				<view class="left">
					<u-icon size="32" name="/static/img/20.png" class="footIcon" v-if="pwdshow"></u-icon>
					<u-icon size="32" name="/static/img/17.png" class="footIcon" v-else></u-icon>
				</view>
				<view class="center">
					<u-input type="password" placeholder="密码" v-model="form.pwd" @input="onInput2"/>
				</view>
				<view class="right">
					<u-icon size="20" name="/static/img/4.png" class="footIcon" v-if="pwdshow"></u-icon>
					<u-icon size="20" name="/static/img/4s.png" class="footIcon" v-else></u-icon>
				</view>
			</view>
			<view class="input_box">
				<view class="left">
					<u-icon size="32" name="/static/img/19.png" class="footIcon" v-if="mobshow"></u-icon>
					<u-icon size="32" name="/static/img/icon_phones.png" class="footIcon" v-else></u-icon>
				</view>
				<view class="center">
					<u-input type="number" placeholder="手机号" v-model="form.phone" @input="onInput3"/>
				</view>
				<view class="right">
					<u-icon size="20" name="/static/img/4.png" class="footIcon" v-if="mobshow"></u-icon>
					<u-icon size="20" name="/static/img/4s.png" class="footIcon" v-else></u-icon>
				</view>
			</view>
			<view class="input_box">
				<view class="left">
					<u-icon size="32" name="/static/img/21.png" class="footIcon" v-if="codeshow"></u-icon>
					<u-icon size="32" name="/static/img/20s.png" class="footIcon" v-else></u-icon>
				</view>
				<view class="center">
					<u-input type="text" placeholder="验证码" v-model="form.code" @input="onInput4"/>
				</view>
				<view class="right">
					<u-icon size="20" name="/static/img/4.png" class="footIcon" v-if="codeshow"></u-icon>
					<u-icon size="20" name="/static/img/4s.png" class="footIcon" v-else></u-icon>
				</view>
			</view>
			<view class="btns">
				<button type="default" class="btn_primary" :disabled="disabled" @click="submit">完成注册</button>
			</view>
		</view>
		<u-toast ref="uToast" />
	</view>
</template>

<script>
	export default {
		data() {
			return {
				zhshow:false,
				pwdshow:false,
				mobshow:false,
				codeshow:false,
				disabled:true,
				form:{
					username:"",
					pwd:"",
					phone:"",
					code:""
				}
			}
		},
		methods: {
			onInput(e){
				if(e!=""){
					this.zhshow = true;
				}else{
					this.zhshow = false;
				}
				this.onshow();
			},
			onInput2(e){
				if(e!=""){
					this.pwdshow = true;
				}else{
					this.pwdshow = false;
				}
				this.onshow();
			},
			onInput3(e){
				if(e!=""){
					this.mobshow = true;
				}else{
					this.mobshow = false;
				}
				this.onshow();
			},
			onInput4(e){
				if(e!=""){
					this.codeshow = true;
				}else{
					this.codeshow = false;
				}
				this.onshow();
			},
			onshow(){
				if(this.form.username!="" && this.form.pwd!="" && this.form.phone!="" && this.form.code!=""){
					this.disabled = false;
				}else{
					this.disabled = true;
				}
			},
			submit(){
				if(this.form.username==""){
					this.$refs.uToast.show({
						title: '请输入账号'
					})
					return false;
				}
				if(this.form.pwd==""){
					this.$refs.uToast.show({
						title: '请输入密码'
					})
					return false;
				}
				if(this.form.phone==""){
					this.$refs.uToast.show({
						title: '请输入手机号'
					})
					return false;
				}
				if(this.form.code==""){
					this.$refs.uToast.show({
						title: '请输入验证码'
					})
					return false;
				}
				uni.hideKeyboard();
				if(this.form.pwd.length<8){
					this.$refs.uToast.show({
						title: '密码长度不能小于八位'
					})
					return false;
				}
				if(!(/^1(3|4|5|6|7|8|9)\d{9}$/.test(this.form.phone))){ 
					uni.showToast({title: '请填写正确手机号码',icon:"none"});
					return false; 
				}
				let pram = {
					username:this.form.username,
					password:this.form.pwd,
					mobile:this.form.phone,
					code:this.form.code
				}
				this.$api.http.register(pram).then((res) => {
					if(res.code == 200){
						this.$refs.uToast.show({
							title: '注册成功',
							url: '/pages/login/login'
						})
					}
				});
			}
		}
	}
</script>

<style lang="scss">
	page{
		background: transparent;
		height: 100%;
	}
.registerbg{
	width: 100%;
	height: 100%;
	background: url(../../static/img/167.png) no-repeat;
	background-size: 100% 100%;
	.head{
		height: 21%;
		position: relative;
		margin-bottom:100rpx;
		.Size{
			position: absolute;
			left: 65rpx;
			bottom: 0;
			font-weight: 500;
			font-size: 50rpx;
		}
	}
	.body{
		margin:0 54rpx;
		.input_box{
			height: 110rpx;
			background: #ffffff;
			border-radius: 10rpx;
			box-shadow: 0px 1px 2px 0px rgba(0,0,0,0.16);
			margin-top:30rpx;
			display: flex;
			align-items: center;
			.left{
				display: flex;
				padding:0 52rpx;
			}
			.center{
				flex:1;
				input{
					color:#707070;
					font-size: 28rpx;
				}
			}
			.right{
				margin-left:20rpx;
				margin-right: 72rpx;
			}
		}
		.btns{
			position: fixed;
			bottom:132rpx;
			left:0;
			z-index: 50;
			width:100%;
			.btn_primary{
				margin:0 120rpx;
				height:92rpx;
				line-height: 92rpx;
				border-radius: 10rpx;
				box-shadow: 0px 1px 2px 0px rgba(0,0,0,0.16);
				font-size: 28rpx;
				color:#ffffff;
				background:#FF9D00;
				&[disabled]:not([type]), &[disabled][type=default]{
					background: #d4d4d4;
				}
				&::after{
					border:0;
				}
			}
		}
	}
}
</style>